.App {
  /* text-align: center; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.black-nav {
  display: flex;
  background-color: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}
.list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.list li {
  margin: 0;
  padding: 20px;
  border-bottom: 1px solid black;
}
.list h2 {
  font-size: 16px;
  margin: 0;
  padding: 0;
  cursor: pointer;
}
.list p {  
  font-size: 12px;
}
.list .btn-like {
  border: none;
  background: none;
  cursor: pointer;
}

.modal {
  background-color: beige;
  margin: 20px;
  padding: 20px;
}
.modal h2 {
  font-size: 16px;  
}
.modal p {
  font-size: 14px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
